.HotAccessories {
    display: flex;
    width: 90vw;
    margin: auto;
}

.HotAccessories div:first-child img {
    cursor: pointer;
    transition: all .5s ease-in-out;
    width: 22vw;
}

.HotAccessories div:first-child img:hover {
    transform: translateY(-.4vmax);
    box-shadow: 0px 6px 15px grey;
}

.HotItemCard0{
    display: none;
}

.HotAccessories div:last-child {
    display: flex;
    flex-wrap: wrap;
}