.preNav {
    background-color: black;
    display: grid;
    grid-template-columns: 5fr 2fr;
    color: white;
}

.preNav div {
    padding: 0.8vmax 4vmax;
}

.preNav div span {
    color: rgba(238, 229, 229, 0.744);
    font-size: .8vmax;
    font-weight: 100;
}

.preNav div a {
    color: rgb(154, 149, 149);
    font-size: .9vmax;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 500;
    margin: 1vmax;
    text-decoration: none;
}

.preNav div a:hover {
    color: white;
}

.shoppingCart {
    margin: 0 5px;
}