.PreFooter{
    margin: 5vmax 0 0 0;
    background-color: white;
    padding: 1vmax;
    padding-bottom: 0;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.PreFooter div{
    align-items: center;
    justify-content: center;
    display: flex;
    color: #777;
    cursor: pointer;
}
.PreFooter div:hover{
    color: #444;
}
.PreFooter span{
    text-align: left;
}

.PreFooter i{
    margin: 2vmax;
}

/* ---------------------PreFooter 2-----------------------  */

.PreFooter2{
    display: grid;
    height: 150px;
    grid-template-columns: repeat(4,1fr);
    display: flex;
    justify-content: space-around;
   padding: 0 5vmax;
}
.PreFooter2>div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.PreFooter2 div p{
    color: rgb(47, 45, 45);
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.5vmax;
    width: 18vmax;
    margin: 0%;
    
}

.PreFooter2 div span{
    color: #333;
    width: 18vmax;
    font-size: 1vmax;

}

.PreFooter2>div>div>input{
    border: none;
    outline: none;
    padding: .5vmax 1vmax;
    background-color: white;
    font-size: 1vmax;
    width: 20.4vmax;
}
.PreFooter2>div>div>button{
    border: none;
    padding: .5vmax;
    background-color: white;
    outline: none;
}

.PreFooter2>div>div{
    border: 1px solid #555;
}
.PreFooter2>div:nth-child(2)>span{
    width: 22vmax;
}

.PreFooter2>div:nth-child(3){
    justify-content: center;
    align-items: flex-end;
}
.PreFooter2>div:nth-child(3)>p{
    width: 14vmax;
}
.PreFooter2>div:nth-child(3)>span{
    width: 14vmax;
}
.PreFooter2>div:nth-child(4){
    flex-direction: row;
}
.PreFooter2>div:nth-child(4)>i{
    cursor: pointer;
    margin: 1vmax;
    color: #777;
}
.PreFooter2>div:nth-child(4)>i:hover{
    color: #444;
}