.nav {
    display: flex;
    justify-content: center;
    height: 2.5rem;
    padding: 1vmax;
    background-color: white;
    align-items: center;
    border-bottom: 1px solid rgb(222, 212, 212);
    height: 5rem;
}

.logoImage {
    color: black;
    background-color: orange;
}

.logo {
    background-color: orangered;
    width: 56px;
    border-radius: 20px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    right: 2vmax;
}

.searchBox {
    position: relative;
    left: 5vmax;
}

.searchBox input {
    border: none;
    border-bottom: 1px solid rgb(182, 173, 173);
    font-size: 16px;
}

.fa-magnifying-glass {
    margin-left: 10px;
    cursor: pointer;
    color: rgba(59, 59, 59, 0.801);
}

.searchBox input:focus{
    outline: none;
}

.navlinks {
    color: rgba(59, 59, 59, 0.801);
    text-decoration: none;
    margin: 1vmax 1.2vmax;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    font-size: 1.1vmax;

}
.navlinks:hover{
    color: orangered;
    text-decoration: none;
}
.fa-magnifying-glass:hover{
    color: orangered;
}

#logoImage{
    transform: translate(-55px);
    transition: all .3s ease-in-out;
}
#logoImage:hover{
    transform: translate(0px);
}