.HotItemCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    width: 31%;
    margin: .7vmax;
    cursor: pointer;
    transition: all .3s ease-in-out;

}

.HotItemCard:hover {
    transform: translateY(-0.4vmax);
    box-shadow: 0px 6px 15px rgb(194, 191, 191);
}

.HotItemCard img:first-child:hover {
    box-shadow: none;
}

.HotItemCard img {
    max-width: 81%;
}

.HotItemCard p {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: rgb(85, 83, 83);
}

.HotItemCard span {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: orangered;
    margin: -1vmax 0 1vmax 0;
}