.starProduct{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1vmax auto;
}
.starProduct>div:last-child{
    display: flex;
    flex-wrap: wrap;
    width: 48%;
}
.starProduct img{
    transition: all .5s;
}

.starProduct img:hover{
    box-shadow: 6px 6px 15px rgba(53, 53, 53, .363);
}