 .VideosCard {
     width: 30vmax;
     height: 48vh;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     margin: .5vmax;
     cursor: pointer;
     background-position: center;
     background-size: cover;
     background-repeat: no-repeat;
 }

 .VideosCard i {
     padding: 2.5vmax;
     transition: all .2s ease-in-out;
     border: none;
     border-radius: 100%;
     background-color: rgb(212, 198, 198);
     opacity: .35;
     color: whitesmoke;
 }
 .VideosCard:hover i{
    opacity: .85;
 }
 .VideosCard p{
    color: white;
    margin: 1vmax;
 }