.ProductReviewCard {
    margin: .5vmax;
    background-color: white;
    padding-bottom: 2vmax;
    cursor: pointer;
    transition: all .5s ease-in-out;
}

.ProductReviewCard:hover {
    transform: translateY(-.4vmax);
    box-shadow: 0px 6px 15px rgb(182, 175, 175);
}

.ProductReviewCard h5 {
    margin: 2vmax 0 .5vmax 2vmax;
    color: #777;
}

.ProductReviewCard span {
    margin: 0 2vmax;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1vmax;
    color: #777;
}

.ProductReviewCard b {
    font-size: 1vmax;
    color: orangered;
}